.onboarding-middle {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    width: 100%;
}

.onboarding-middle__pic-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 50dvh;
    overflow: hidden;
    background-size: cover;
    padding: 20px 0;
}

.onboarding-middle__pic-wrapper_type_man,
.onboarding-middle__pic-wrapper_type_referal {
    background-image: url(../../assets/img/onboarding-bg-man.png);
}

.onboarding-middle__pic-wrapper_type_woman {
    background-image: url(../../assets/img/onboarding-bg-woman.png);
}

.onboarding-middle__pic-wrapper_type_woman .onboarding-middle__pic {
    transform: translateX(-20px);
}

.onboarding-middle__pic {
    /* width: 350px; */
    height: 46vh;
    width: auto;
    /* max-width: calc(100% - 20px); */
}

.onboarding-middle__content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 50dvh;
}

.onboarding-middle__logo {
    margin-bottom: 20px;
}

.onboarding-middle__title {
    margin-bottom: 20px;
}

.onboarding-middle__text {
    margin-bottom: 25px;
}

.onboarding-middle__button {
    margin-top: auto;
    flex-shrink: 0;
}

.onboarding-middle__man-buttons {
    display: flex;
    align-items: center;
    grid-gap: 10px;
    margin-top: auto;
}

.onboarding-middle__back-button {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    background-color: #E2E0DF;
    border-radius: 50%;
}