.answers{
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    padding: 20px;
    background-color: var(--pink);
    background-image: url(../../assets/img/answers-pic-bg.png), url(../../assets/img/answers-bg-pattern.jpg);
    background-position: 100% 0, center 0;
    background-repeat: no-repeat, repeat;
    background-size: 100%, 400px;
}

.answers__logo{
    margin-bottom: 20px;
}

.answers-list__answers{
    display: flex;
    grid-gap: 10px;
    width: 100%;
    /* display: grid; */
}

.answers__title{
    margin-bottom: 15px;
}

.answers__text{
    font-size: 22px;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 30px;
}

.answer :global(input) {
    display: none;
}

.answer__image {
    max-width: 100%;
    object-fit: contain;
    /* margin-bottom: 20px; */
    margin-bottom: auto;
}

.answer_type_pic :global(label),
.answer_type_background :global(label) {
    background-color: #fff;
    border-radius: 12px;
    overflow: hidden;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    position: relative;
    padding: 10px;
    border: 3px solid #000000;
    transition: .3s;
    /* height: 100%; */
    height: calc(50vw);
}

.answer_type_pic :global(label){
    max-width: 50%;
    width: 100%;
}

.answer_type_color :global(label) {
    display: flex;
    align-items: center;
    transition: .3s;
}

.answer__color {
    width: 70px;
    height: 70px;
    display: flex;
    border-radius: 35px;
    flex-shrink: 0;
    margin-right: 10px;
    border: 3px solid #fff;
    transition: .3s;
}

.answer__index {
    font-size: 15px;
    font-weight: 500;
}

.answer__title {
    font-size: 14px;
    font-weight: 500;
    text-align: left;
    height: 60px;
}

.answer__index {
    font-size: 15px;
    position: absolute;
    left: 10px;
    bottom: 10px;
}

.answers-list{
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
}

.answers-list__item{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.answer-list__index{
    font-size: 22px;
    line-height: 1;
    font-weight: 700;
    margin-bottom: 10px;
    margin-right: auto;
}

.answer_type_pic :global(input:checked + label),
.answer_type_background :global(input:checked + label) {
    border-color: #000000;
}

.answer_type_color :global(input:checked + label) .answer__color {
    border-color: #000000;
}

.answer_type_color .answer__title {
    font-size: 24px;
}

.answer_type_background :global(label) {
    height: calc(100vw/2);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.answer_type_check :global(label) {
    display: flex;
    align-items: center;
    padding: 15px 20px;
    /* border: 3px solid #000000; */
    border-radius: 12px;
}

.answer_type_check .answer__title {
    font-size: 20px;
    font-weight: 500;
}

.answer_type_check :global(label) {
    background-color: #000000;
}

.answer_type_check :global(label) .answer__title {
    color: #fff;
}

.answer_type_check{
    flex-direction: column;
}

.answer_type_background{
    display: grid;
    grid-template-columns: repeat(2, 1fr); 
    width: 100%;
}

.answers__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    grid-gap: 10px;
    margin-top: 25px;
}

.answers__bottom-button{
    height: 55px;
    width: 55px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 50%;
    flex-shrink: 0;
}

.answers-list__item_kal{
    background-image: url(../../assets/img/kal-bg.png);
    background-repeat: no-repeat;
    background-size: 255px 105px;
    background-position: center 0;
}

.answers-list__item_kal .answers-list__answers{
    justify-content: center;
}

.answers-list__item_pomada :global(label){
    width: auto;
}

.answers-list__item_pomada .answers-list__answers{
    justify-content: center;
}

.answers-list__item_pomada :global(img){
    height: calc(50vw - 10px);
}