.onboarding-start {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    padding: 20px;
    background-image: url(../../assets/img/onboarding-pic1.png), url(../../assets/img/onboarding-bg1.png);
    background-color: var(--pink);
    background-size: 450px, cover;
    background-position: center calc(100% - 25vh), center 100%;
    background-repeat: no-repeat;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
}


.onboarding-start__logo {
    margin-right: auto;
    margin-bottom: 20px;
}

.onboarding-start__buttons {
    display: flex;
    flex-direction: column;
    margin-top: auto;
    grid-gap: 25px;
    padding: 0 20px 20px;
}

.onboarding-start__title-wrapper {
    position: relative;
    margin-bottom: 45px;
}

.onboarding-start__title {
    position: relative;
    z-index: 2;
}

.onboarding-start__title-wrapper:after {
    content: '';
    position: absolute;
    left: -20px;
    left: -20px;
    top: -30px;
    width: 380px;
    height: 160px;
    background-image: url(../../assets/img/main-title-bg.png);
    background-repeat: no-repeat;
    background-size: contain;
}

.loader-wrapper{
    width: 100%;
    height: 100dvh;
    display: flex;
    align-items: center;
    justify-content: center;
    background-image: url(../../assets/img/onboarding-bg1.png);
    background-position: center 100%;
    background-size: cover;
}

.loader {
    width: 50px;
    aspect-ratio: 1;
    border-radius: 50%;
    border: 8px solid #0000;
    border-right-color: var(--green);
    position: relative;
    animation: l24 1s infinite linear;
    filter: drop-shadow(0 0 10px #00000066);
}

.loader:before,
.loader:after {
    content: "";
    position: absolute;
    inset: -8px;
    border-radius: 50%;
    border: inherit;
    animation: inherit;
    animation-duration: 2s;
}

.loader:after {
    animation-duration: 4s;
}

@keyframes l24 {
    100% {
        transform: rotate(1turn)
    }
}