.processing {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    padding: 20px;
    background-image: url(../../assets/img/processing-pic1.png), url(../../assets/img/processing-pic2.png), url(../../assets/img/onboarding-bg1.png);
    background-color: var(--pink);
    background-size: 90%, 90%, cover;
    background-position: center 10%, center 90%, center 100%;
    background-repeat: no-repeat;
    animation: bgFloating 2s infinite;
    overflow: hidden;
    width: 100%;
    box-sizing: border-box;
}

@keyframes bgFloating {
    0%{
        background-position: center 8%, center 95%, center 100%;
    }

    50%{
        background-position: center 10%, center 93%, center 100%;
    }

    100%{
        background-position: center 8%, center 95%, center 100%;
    }
}


.processing__logo {
    margin-right: auto;
    margin-bottom: 20px;
}

.processing__title-wrapper {
    margin-top: auto;
    margin-bottom: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-image: url(../../assets/img/processing-title-bg.png);
    background-size: contain;
    width: 320px;
    height: 320px;
    margin-left: auto;
    margin-right: auto;
    /* background-image: url(,,); */
}