.final {
    display: flex;
    flex-direction: column;
    min-height: 100dvh;
    width: 100%;
    background-color: #fff;
}

.final__pic-wrapper {
    /* background-color: var(--pink); */
    background-image: url(../../assets/img/onboarding-bg-man.png);
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 50dvh;
    overflow: hidden;
}

.final__pic {
    /* width: 350px; */
    max-height: 100%;
    width: auto;
    max-width: calc(100%);
}

.final__content {
    padding: 20px;
    display: flex;
    flex-direction: column;
    min-height: 50dvh;
}

.final__logo {
    margin-bottom: 20px;
}

.final__title {
    margin-bottom: 20px;
}

.final__percent{
    font-size: 22px;
    text-align: left;
    font-weight: 700;
    margin-bottom: 20px;
}

.final__text-first {
    margin-bottom: 25px;
}

.final__text-second {
    margin-top: 20px;
    margin-bottom: 20px;
}

.final__text-third {
    margin-top: 15px;
    margin-bottom: 20px;
}

.final__button {
    margin-top: auto;
    flex-shrink: 0;
}

.copy-block{
    display: flex;
    align-items: center;
}

.copy-block__ticket{
    width: max-content;
    height: 36px;
    background-image: url(../../assets/img/ticket.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    padding: 0 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: 10px;
}